@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
}
.sideBar {
    background-color: #2C394B;
    color: #F7F6F2;
    display: flex;
    flex-direction: column;
    font-size: 17px;
    height: 100%;
    margin: 0;
    overflow: auto;
    padding: 0;
    position: fixed;
    width: 20%;
}

@media (max-width: 700px) {
    .sideBar {
        width: 100px;

    }
}
.sidebarElement {
    align-items: center;
    color: #F7F6F2;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    cursor: pointer;
    text-decoration: none;
    transition: all 1s;
}

.iconTeam {
    height: 24px;
}

.sidebarElement:hover {
    background-color: rgba(255, 77, 41, 0.588);
    box-shadow: rgba(255, 77, 41, 0.1) 0px 8px 24px;
}

@media (max-width: 700px) {
    .teamTitle{
        display: none;
    }
}
.footer {
    background-color: #082032;
    bottom: 0;
    position: fixed;
    width: 100%;
}
.score{
    background-color: #F7F6F2;
    border-radius: 20px;
    color: #000;
    text-align: center;
    margin: 10px;
    padding: 5px;
    width: 40%;
}

@media (max-width: 768px) {
    .score{
        width: 100%;
    }

}
.matches{
    background-color: #334756;
    color: #F7F6F2;
    display: flex;
    font-size: 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 21%;
    padding: 1px 16px;
    width: calc(100% - 320px);
    /* height: 550px; */
}

@media (max-width: 700px) {
    .matches{
        font-size: 15px;
        margin-left: 21%;
        width: calc(100% - 150px);
    }

}

@media (max-width: 480px) {
    .matches{
        margin-left: 22%;
        width: calc(100% - 130px);
    }

}
body {
    background-color: #082032;
    font-family: 'Poppins', sans-serif;
}

span {
    height: 10%;
}
